.btn {
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: var(--btnColor);
    text-transform: uppercase;
    letter-spacing: 4px;
    text-decoration: none;
    font-size: 3vw;
    font-weight: bold;
    overflow: hidden;
    transition: 0.2s;
    cursor: pointer;
    border-radius: 4px;
    text-shadow: 0 0 6px rgb(107, 110, 110);
}

.btn:hover {
    color: var(--txtHoverColor);
    background: var(--btnColor);
    box-shadow: 0 0 10px var(--btnColor), 0 0 40px var(--btnColor), 0 0 80px var(--btnColor);
    transition-delay: 1s;
    text-shadow: none;
}

.btn .border {
    position: absolute;
    display: block;
}

.btn .border:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, var(--btnColor));
}

.btn:hover .border:nth-child(1) {
    left: 100%;
    transition: 1s;
}

.btn .border:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, var(--btnColor));
}

.btn:hover .border:nth-child(3) {
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}

.btn .border:nth-child(2) {
    right: 0;
    top: -100%;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, var(--btnColor));
}

.btn:hover .border:nth-child(2) {
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.btn .border:nth-child(4) {
    left: 0;
    bottom: -100%;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, var(--btnColor));
}

.btn:hover .border:nth-child(4) {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}